.row {
    display: flex;
    flex-wrap: wrap;
    padding: 2%;
    text-align: center;
    justify-content: center;
    vertical-align: middle;
}
  
.column {
    flex: 50% 1;
    max-width: 50%;
    padding: 1%;
    box-sizing: border-box; 
    justify-content: center;
    
}
  
.buttonNext{
    position: absolute;
    background-color: #FF8A65;
    color: #fff;
    font-weight: bolder;
    border-radius: 30px;
    font-size: 18px;
    width: 200px;
    height: 50px;
    margin-left: -28%;
    padding-top: 10px;

}
.buttonNext:hover{
    background-color: #fff;
    color:  #FF8A65;
